import { Button, CardMedia } from "@mui/material";
import React from "react";
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import FotoAvatar from "../Images/FotoAvatar.jfif"
import { Card } from "react-bootstrap";
import { textAlign } from "@mui/system";
import Stack from '@mui/material/Stack';
import CircularProgress from '@mui/material/CircularProgress';

const CartaPresentacion=()=>{
    return(
        <div>
            <Box sx={{ width: '100%', m:'auto', textAlign:'center',bgcolor:"#D2D2E3"}}>
                <CardMedia component="img" image={FotoAvatar} sx={{width:200, borderRadius:"50%", m:'auto', p:2}}/>
                <Typography 
                    fontSize= {18} 
                    fontFamily={ 'monospace'} 
                    variant="body1" 
                    fontWeight = {500}
                    letterSpacing= {'.15rem'}
                    gutterBottom 
                    sx={{p:1, maxWidth:850, m:'auto'}}
                >
                Soy <b>Ingeniero Electrnico</b> con orientacin al Control. Actualmente participo en la <b>elaboracin de simuladores</b> para el Ejrcito Argentino. <br></br>
                <br></br>
                Soy una persona <b>perfeccionista y voluntariosa</b>, con una gran capacidad de aprendizaje y muy dedicado al trabajo. <br></br>
                <br></br>
                A lo largo de mi carrera he intentado adquirir tanto conocimientos tcnicos como humanos realizando especializaciones y cursos en ambas reas. Tengo un <b>Mster en Recursos Humanos y Gestin del Conocimiento</b> de la Universidad Europea del Atlntico y un <b> Mster en Administracin Gerencial</b> de la Universidad Internacional Iberoamericana. <br></br>
                <br></br>
                He realizado <b>cursos de desarrollo web Full Stack, RPA, desarrollo de videojuegos, programacin de microcontroladores, programacin orientada a objetos</b> y me desempe como Auxiliar de Laboratorio de Mecatrnica.<br></br>
                <br></br>
                Egres del Colegio Militar de la Nacin Argentina como <b>Licenciado en Gestin y Conduccin Operativa</b>, alcanzando el grado de Capitn. <br></br>
                <br></br>
                En 2011, fui designado como Jefe de Patrulla en Chipre por las Naciones Unidas y realic la mayora de sus cursos de capacitacin, siendo <b>reconocido por el Comandante de la ONU en Chipre por mi desempeo profesional</b>.<br></br>
                <br></br>
                En 2015 realic un especializacin en Ciencias Militares en la Escuela de perfeccionamiento de Oficiales en Ro de Janeiro, Brasil. <br></br>
                <br></br>
                Me desempe como profesor en la Escuela de Artillera del Ejrcito e Instructor en la Escuela de Suboficiales. <br></br>
                <br></br>
                Puedo comunicarme en espaol, ingls o portugus. <br></br>
                </Typography>
                <Typography textAlign="center"  /*LinkedIn */>
                    <Button variant="outlined" href="/" sx={{m:2}}>Volver</Button>
                </Typography>
            </Box>
        </div>
    )
}

export default CartaPresentacion;